<template>
  <!-- eslint-disable max-len -->
  <!-- eslint-disable vuejs-accessibility/label-has-for -->
  <aside class="filter">
    <form class="filter__form form" action="#" method="get" @submit.prevent="submit">
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="min-price" v-model.number="currentPriceFrom">
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="max-price" v-model.number="currentPriceTo">
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <label class="form__label form__label--select">
          <select class="form__select" type="text" name="category" v-model.number="currentCategoryId">
            <option value="0">Все категории</option>
            <option :value="category.id" v-for="category in categories" :key="category.id">{{ category.title }}</option>
          </select>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Цвет</legend>
        <ul class="colors colors--black">
          <li class="colors__item" v-for="item in colors" :key="item.id">
            <label class="colors__label">
              <input class="colors__radio sr-only" type="radio" name="color" :value="item.id" v-model="currentColor">
              <span class="colors__value" :style="{ 'background-color': item.code }">
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Материал</legend>
        <ul class="check-list">
          <li class="check-list__item" v-for="material in materials" :key="material.id">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="material" :value="material.id"
                v-model.number="currentMaterial">
              <span class="check-list__desc">
                {{ material.title }}
                <span>({{ material.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Коллекция</legend>
        <ul class="check-list">
          <li class="check-list__item" v-for="season in collections" :key="season.id">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="collection" :value="season.id"
                v-model.number="currentCollection">
              <span class="check-list__desc">
                {{ season.title }}
                <span>({{ season.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit" @click="checkFields">
        Применить
      </button>
      <button class="filter__reset button button--second" type="button" @click.prevent="reset(); hideButton()"
        v-show="showButton">
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>
/* eslint-disable max-len */
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  data() {
    return {
      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCategoryId: 0,
      currentColor: 0,
      currentMaterial: [],
      currentCollection: [],

      categoriesData: null,
      materialsData: null,
      collectionsData: null,
      colorsData: null,
      showButton: false,
    };
  },
  props: ['priceFrom', 'priceTo', 'categoryId', 'materialId', 'collectionId', 'colorId'],
  computed: {
    categories() {
      return this.categoriesData ? this.categoriesData.items : [];
    },
    materials() {
      return this.materialsData ? this.materialsData.items : [];
    },
    collections() {
      return this.collectionsData ? this.collectionsData.items : [];
    },
    colors() {
      return this.colorsData ? this.colorsData.items : [];
    },
  },
  watch: {
    priceFrom(value) {
      this.currentPriceFrom = value;
      if (value > 0) this.showButton = true;
    },
    priceTo(value) {
      this.currentPriceTo = value;
      if (value > 0) this.showButton = true;
    },
    categoryId(value) {
      this.currentCategoryId = value;
      if (value > 0) this.showButton = true;
    },
    materialId(value) {
      this.currentMaterial = value;
      if (value > 0) this.showButton = true;
    },
    collectionId(value) {
      this.currentCollection = value;
      if (value > 0) this.showButton = true;
    },
    colorId(value) {
      this.currentColor = value;
      if (value > 0) this.showButton = true;
    },
  },
  methods: {
    submit() {
      this.$emit('update:priceFrom', this.currentPriceFrom);
      this.$emit('update:priceTo', this.currentPriceTo);
      this.$emit('update:categoryId', this.currentCategoryId);
      this.$emit('update:materialId', this.currentMaterial);
      this.$emit('update:collectionId', this.currentCollection);
      this.$emit('update:colorId', this.currentColor);
    },
    reset() {
      this.$emit('update:priceFrom', 0);
      this.$emit('update:priceTo', 0);
      this.$emit('update:categoryId', 0);
      this.$emit('update:materialId', []);
      this.$emit('update:collectionId', []);
      this.$emit('update:colorId', 0);
    },
    loadCategories() {
      axios.get(`${API_BASE_URL}/api/productCategories`)
        .then((response) => { this.categoriesData = response.data; });
    },
    loadMaterials() {
      axios.get(`${API_BASE_URL}/api/materials`)
        .then((response) => { this.materialsData = response.data; });
    },
    loadCollections() {
      axios.get(`${API_BASE_URL}/api/colors`)
        .then((response) => { this.colorsData = response.data; });
    },
    loadColors() {
      axios.get(`${API_BASE_URL}/api/seasons`)
        .then((response) => { this.collectionsData = response.data; });
    },
    selectCategory() {
      if (!this.categoryId) return;
      this.currentCategoryId = this.categoryId;
    },
    hideButton() {
      this.showButton = false;
    },
    checkFields() {
      if (this.currentCategoryId || this.currentPriceTo || this.currentCategoryId || this.currentMaterial.length || this.currentCollection.length) this.showButton = true;
      else this.showButton = false;
    },
  },
  created() {
    this.loadCategories();
    this.loadMaterials();
    this.loadCollections();
    this.loadColors();
    this.selectCategory();
    this.checkFields();
  },
};
</script>
