          </p>

          <router-link tag="button" class="cart__button button button--primery" type="submit" :to="{name: 'order'}" :disabled="!totalPrice">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
import CartItem from '@/components/CartItem.vue';
import wordFormat from '@/helpers/wordFormat';

export default {
  components: { CartItem },
  filters: {
    numberFormat,
  },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
    pluralize() {
      return wordFormat(this.products.length);
    },
  },
};
</script>
