/* eslint-disable max-len */
/* eslint-disable no-param-reassign */
import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    isCartLoading: false,
    isCartLoadingFailed: false,
    orderInfo: null,
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateCartProductAmount(state, {
      productId, amount, colorId, sizeId,
    }) {
      const item = state.cartProducts.find((elem) => elem.productId === productId && elem.colorId === colorId && elem.sizeId === sizeId);
      if (item) {
        item.amount = amount;
      }
    },
    deleteCartProduct(state, basketItemId) {
      state.cartProducts = state.cartProducts.filter((p) => p.basketItemId !== basketItemId);
    },
    updateUserAccessKey(state, accesKey) {
      state.userAccessKey = accesKey;
    },
    updateCardProductsData(state, items) {
      state.cartProductsData = items;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => ({
        productId: item.product.id,
        amount: item.quantity,
        basketItemId: item.id,
        colorId: item.color.color.id,
        sizeId: item.size.id,
      }));
    },
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map((item) => ({
        ...item,
        product: state.cartProductsData.find((product) => product.product.id === item.productId && product.size.id === item.sizeId && product.color.color.id === item.colorId),
      }));
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce((acc, item) => acc + item.amount * item.product.price, 0);
    },
  },
  actions: {
    loadOrderInfo(context, orderId) {
      return axios.get(`${API_BASE_URL}/api/orders/${orderId}`, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          context.commit('updateOrderInfo', response.data);
        });
    },
    loadCart(context) {
      context.state.isCartLoading = true;
      context.state.isCartLoadingFailed = false;
      return axios.get(`${API_BASE_URL}/api/baskets`, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
            context.commit('updateUserAccessKey', response.data.user.accessKey);
          }
          context.commit('updateCardProductsData', response.data.items);
          context.commit('syncCartProducts');
          context.state.isCartLoading = false;
        })
        .catch(() => { context.state.isCartLoadingFailed = true; context.state.isCartLoading = false; });
    },

    addProductToCart(context, {
      productId, colorId, sizeId, amount,
    }) {
      context.state.isCartLoading = true;
      context.state.isCartLoadingFailed = false;
      return axios.post(`${API_BASE_URL}/api/baskets/products`, {
        productId,
        colorId,
        sizeId,
        quantity: amount,
      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          context.commit('updateCardProductsData', response.data.items);
          context.commit('syncCartProducts');
          context.state.isCartLoading = false;
        }).catch(() => { context.state.isCartLoadingFailed = true; context.state.isCartLoading = false; });
    },

    updateCartProductAmount(context, {
      productId, colorId, sizeId, amount, basketItemId,
    }) {
      context.commit('updateCartProductAmount', {
        productId, colorId, sizeId, amount,
      });

      if (amount < 1) {
        return;
      }

      // eslint-disable-next-line consistent-return
      return axios.put(`${API_BASE_URL}/api/baskets/products`, {
        basketItemId,
        quantity: amount,
      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          context.commit('updateCardProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },

    deleteCartProduct(context, basketItemId) {
      return axios.delete(`${API_BASE_URL}/api/baskets/products?userAccessKey=${context.state.userAccessKey}`, {
        data: {
          basketItemId,
        },
      })
        .then((response) => {
          context.commit('updateCardProductsData', response.data.items);
          context.commit('syncCartProducts');
        });
    },
  },
});
