<template>
<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.color.gallery ? item.product.color.gallery[0].file.url : ''" width="120" height="120"
        :alt="item.product.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.product.title }}
    </h3>
    <p class="product__info product__info--color">
      Цвет:
      <span>
        <i :style="{'background-color': item.product.color.color.code}"></i>
        {{ item.product.color.color.title }}
      </span>
    </p>
    <p class="size">Размер: <span style="color: #000">{{ item.product.size.title }}</span></p>
    <span class="product__code">
      Артикул: {{ item.product.product.id }}
    </span>

    <ProductCounter class="product__counter" v-model="amount"/>

    <b class="product__price">
      {{ item.product.price * item.amount | numberFormat }} ₽
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteProductApi(item.basketItemId); deleteProduct(item.product.id)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
/* eslint-disable max-len */
import numberFormat from '@/helpers/numberFormat';
import { mapMutations, mapActions } from 'vuex';
import ProductCounter from './ProductCounter.vue';

export default {
  components: { ProductCounter },
  props: ['item'],
  filters: {
    numberFormat,
  },
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', {
          productId: this.item.productId, amount: value, colorId: this.item.colorId, sizeId: this.item.sizeId, basketItemId: this.item.product.id,
        });
      },
    },
  },
  methods: {
    ...mapMutations({ deleteProduct: 'deleteCartProduct' }),
    ...mapActions({ deleteProductApi: 'deleteCartProduct' }),
  },
};
</script>

<style>
.product {
  grid-template-rows: repeat(4,min-content);
}
.size {
  grid-column: 2/3;
  grid-row: 3/4;
  margin: 0;
  font-size: 14px;
  line-height: 1;
  color: #737373;
}
.product__code {
  grid-column: 2/3;
  grid-row: 4/5;
}
.product__pic, .product__title {
  grid-row: 1/5;
}
</style>
