<template>
  <!-- eslint-disable max-len -->
  <!-- eslint-disable vuejs-accessibility/label-has-for -->
  <main class="content container">
    <div class="content__top">

      <div class="content__row">
        <h1 class="content__title">
          Каталог
        </h1>
        <span class="content__info">
          {{ this.countProducts }} {{ pluralize }}
        </span>
      </div>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategory"
        :material-id.sync="filterMaterials" :collection-id.sync="filterCollections" :colorId.sync="filterColors" />
      <section class="catalog">
        <div v-if="productLoading" class="preloader">Загрузка товаров...</div>
        <div v-if="productLoadingFailed" class="preloader">Произошла ошибка при загрузке товаров <button class="error-btn"
            @click.prevent="loadProducts">Попробовать еще раз</button></div>
        <ProductList :style="{ opacity: productLoading ? .2 : 100 }" :products="products" />
        <BasePagination v-model="page" :count="countProducts" :per-page="productPerPage" />
      </section>
    </div>
  </main>
</template>

<script>
/* eslint-disable max-len */
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import wordFormat from '@/helpers/wordFormat';

export default {
  components: { ProductList, BasePagination, ProductFilter },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategory: 0,
      filterColors: 0,
      filterMaterials: [],
      filterCollections: [],

      page: 1,
      productPerPage: 12,

      productsData: null,
      productLoading: false,
      productLoadingFailed: false,
    };
  },
  computed: {
    products() {
      return this.productsData ? this.productsData.items : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
    pluralize() {
      return wordFormat(this.countProducts);
    },
  },
  methods: {
    loadProducts() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios.get(`${API_BASE_URL}/api/products`, {
          params: {
            page: this.page,
            limit: this.productPerPage,
            categoryId: this.filterCategory,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
            materialIds: this.filterMaterials,
            seasonIds: this.filterCollections,
            colorIds: this.filterColors,
          },
        })
          .then((response) => { this.productsData = response.data; })
          .catch(() => { this.productLoadingFailed = true; })
          .then(() => { this.productLoading = false; });
      }, 0);
    },
    selectCategory() {
      if (this.$route.query.category) {
        this.filterCategory = this.$route.query.category;
        this.$router.push({ name: 'main', query: {} });
      }
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategory() {
      this.loadProducts();
    },
    filterMaterials() {
      this.loadProducts();
    },
    filterCollections() {
      this.loadProducts();
    },
    filterColors() {
      this.loadProducts();
    },
  },
  created() {
    this.loadProducts();
    this.selectCategory();
  },
};
</script>

<style>
.preloader {
  font-size: 20px;
  margin-bottom: 20px;
}

.error-btn {
  padding: 3px 10px;
  background-color: #e02d71;
  border: 0;
  color: #fff;
  font-size: 13px;
  cursor: pointer;
}</style>
