<template>
  <!-- eslint-disable max-len -->
  <!-- eslint-disable vuejs-accessibility/label-has-for -->
  <!-- eslint-disable vuejs-accessibility/form-control-has-label -->
  <div>
  <div class="container content" v-if="productLoading">Загрузка товара...</div>
  <div class="container content" v-else-if="productLoadingFailed">Не удалось загрузить товар <button class="error-btn" @click.prevent="loadProduct">Попробовать еще раз</button></div>
  <main v-else class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" href="#" :to="{name: 'main', query:{ category: category.id }}" >
            {{ category.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img width="570" height="570" :src="findImage" :alt="product.title">
        </div>
        <ul class="pics__list">
          <li class="pics__item" v-for="img in product.colors" :key="img.id">
            <a href="#" class="pics__link" :class="{'pics__link--current': colorId === img.id}" @click.prevent="changeImage(img.id)">
              <img width="98" height="98" :src="img.gallery ? img.gallery[0].file.url : notFoundImg" :alt="product.title">
            </a>
          </li>
        </ul>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{ product.id }}</span>
        <h2 class="item__title">
          {{ product.title }}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="addToCart">
            <div class="item__row item__row--center">

              <ProductCounter v-model="productAmount"/>

              <b class="item__price">
                {{ product.price | numberFormat}} ₽
              </b>
            </div>

            <div class="item__row">
              <fieldset class="form__block">
                <legend class="form__legend">Цвет</legend>
                <ul class="colors colors--black">
                  <li class="colors__item" v-for="color in product.colors" :key="color.id">
                    <label class="colors__label">
                      <input class="colors__radio sr-only" type="radio" name="color-item" :value="color.id" v-model="colorId">
                      <span class="colors__value" :style="{'background-color': color.color.code}" style="border: 1px solid #D3D3D3">
                      </span>
                    </label>
                  </li>
                </ul>
              </fieldset>

              <fieldset class="form__block">
                <legend class="form__legend">Размер</legend>
                <label class="form__label form__label--small form__label--select">
                  <select class="form__select" type="text" name="category" v-model="productSize">
                    <option :value="size.id" v-for="size in product.sizes" :key="size.id">{{size.title}}</option>
                  </select>
                </label>
              </fieldset>
            </div>

            <button class="item__button button button--primery" type="submit" :disabled="productAddSending">
              В корзину
            </button>

            <div v-show="productAdded" style="margin-top: 10px;">Товар добавлен в корзину</div>
            <div v-show="productAddSending" style="margin-top: 10px;">Добавляем товар...</div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link" href="#" :class="{'tabs__link--current': !deliveryInfo}" @click.prevent="deliveryInfo = false">
              Информация о товаре
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#" :class="{'tabs__link--current': deliveryInfo}" @click.prevent="deliveryInfo = true">
              Доставка и возврат
            </a>
          </li>
        </ul>

        <div class="item__content" v-show="!deliveryInfo">
          <h3>Состав:</h3>

          <p>
            60% хлопок<br>
            30% полиэстер<br>
          </p>

          <h3>Уход:</h3>

          <p>
            Машинная стирка при макс. 30ºC короткий отжим<br>
            Гладить при макс. 110ºC<br>
            Не использовать машинную сушку<br>
            Отбеливать запрещено<br>
            Не подвергать химчистке<br>
          </p>

        </div>
        <ProductDeliveryInfo v-show="deliveryInfo"/>
      </div>
    </section>
  </main>
</div>
</template>

<script>
/* eslint-disable max-len */
import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';
import ProductCounter from '@/components/ProductCounter.vue';
import axios from 'axios';
import { API_BASE_URL, NOT_FOUND_IMG } from '@/config';
import { mapActions } from 'vuex';
import ProductDeliveryInfo from '@/components/ProductDeliveryInfo.vue';

export default {
  components: { ProductCounter, ProductDeliveryInfo },
  data() {
    return {
      productAmount: 1,
      productsData: null,
      productLoading: false,
      productLoadingFailed: false,
      colorId: null,
      productSize: null,

      productAdded: false,
      productAddSending: false,
      deliveryInfo: false,
    };
  },
  filters: {
    numberFormat,
  },
  computed: {
    product() {
      return this.productsData;
    },
    category() {
      return this.productsData.category;
    },
    findImage() {
      return this.product.colors.find((item) => item.id === this.colorId).gallery ? this.product.colors.find((item) => item.id === this.colorId).gallery[0].file.url : NOT_FOUND_IMG;
    },
    notFoundImg() {
      return NOT_FOUND_IMG;
    },
    colorIdForCart() {
      return this.product.colors.find((item) => item.id === this.colorId).color.id;
    },
  },
  methods: {
    ...mapActions(['addProductToCart']),
    gotoPage,
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;

      this.addProductToCart({
        productId: this.product.id, colorId: this.colorIdForCart, sizeId: this.productSize, amount: this.productAmount,
      })
        .then(() => {
          this.productAdded = true;
          this.productAddSending = false;
        });
    },
    loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      axios.get(`${API_BASE_URL}/api/products/${this.$route.params.id}`)
        .then((response) => { this.productsData = response.data; this.colorId = response.data.colors[0].id; this.productSize = response.data.sizes[0].id; })
        .catch(() => { this.productLoadingFailed = true; })
        .then(() => { this.productLoading = false; });
    },
    changeImage(colorId) {
      this.colorId = colorId;
    },
  },
  watch: {
    '$route.params.id': {
      handler() {
        this.loadProduct();
      },
      immediate: true,
    },
  },
};
</script>

<style>
.error-btn{
  padding: 3px 10px;
  background-color: #e02d71;
  border: 0;
  color: #fff;
  font-size: 13px;
  cursor: pointer;
}
</style>
