<template>
<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" href="#" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" href="#" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">
          Оформление заказа
        </h1>
      </div>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">

            <BaseFormText title="ФИО" placeholder="Введите ваше полное имя" :error="formError.name" v-model="formData.name"/>

            <BaseFormText title="Адрес доставки" placeholder="Введите ваш адрес" :error="formError.address" v-model="formData.address"/>

            <BaseFormText title="Телефон" placeholder="Введите ваш телефон" :error="formError.phone" v-model="formData.phone"/>

            <BaseFormText title="Email" placeholder="Введи ваш Email" :error="formError.email" v-model="formData.email"/>

            <BaseFormTextarea title="Комментарий к заказу" placeholder="Ваши пожелания" :error="formError.comment" v-model="formData.comment"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <div v-if="!deliveryData">Получаем данные с сервера...</div>
            <ul class="cart__options options">
              <li class="options__item" v-for="item in deliveryData" :key="item.id">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" :value="item.id" v-model="formData.deliveryTypeId">
                  <span class="options__value">
                    {{ item.title }} <b>{{ item.price }} ₽</b>
                  </span>
                </label>
              </li>
              <span class="pay-error" v-if="formError.deliveryTypeId">{{ formError.deliveryTypeId }}</span>
            </ul>

            <div v-if="paymentsLoading">Получаем данных с сервера...</div>
            <div v-if="paymentsData">
            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item" v-for="item in paymentsData" :key="item.id">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" :value="item.id" v-model="formData.paymentTypeId">
                  <span class="options__value">
                    {{ item.title }}
                  </span>
                </label>
              </li>
            </ul>
            <span class="pay-error" v-if="formError.paymentTypeId">{{ formError.paymentTypeId }}</span>
            </div>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="product in products" :key="product.basketItemId">
              <h3>{{ product.product.product.title }} <span v-if="product.product.quantity > 1" style="color: #000; font-weight: 700;">X {{ product.product.quantity }}</span></h3>
              <b>{{ product.product.product.price * product.product.quantity | numberFormat }} ₽</b>
              <span>Артикул: {{ product.product.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>{{ this.price }}</b></p>
            <p>Итого: <b>{{ this.products.length }}</b> {{ pluralize }} на сумму <b>{{ totalPrice + +this.price | numberFormat }} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit" :disabled="orderLoading">
            Оформить заказ
          </button>
          <div v-if="orderLoading">Идёт оформление заказа...</div>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
/* eslint-disable max-len */
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
import wordFormat from '@/helpers/wordFormat';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  components: { BaseFormText, BaseFormTextarea },
  data() {
    return {
      formData: {
        deliveryTypeId: null,
        paymentTypeId: null,
      },
      formError: {},
      formErrorMessage: '',
      orderLoading: false,
      deliveryData: null,
      paymentsData: null,
      paymentsLoading: false,
      price: null,
    };
  },
  filters: {
    numberFormat,
  },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
    pluralize() {
      return wordFormat(this.products.length);
    },
  },
  watch: {
    // eslint-disable-next-line func-names
    'formData.deliveryTypeId': function () {
      this.loadPayments();
      this.deliveryPrice();
    },
  },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      this.orderLoading = true;
      axios.post(`${API_BASE_URL}/api/orders`, {
        ...this.formData,
      }, {
        params: {
          userAccessKey: this.$store.state.userAccessKey,
        },
      })
        .then((response) => {
          this.$store.commit('resetCart');
          this.orderLoading = false;
          this.$store.commit('updateOrderInfo', response.data);
          this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
          this.orderLoading = false;
        });
    },
    loadDeliveries() {
      axios.get(`${API_BASE_URL}/api/deliveries`)
        .then((response) => { this.deliveryData = response.data; });
    },
    loadPayments() {
      this.formData.paymentTypeId = null;
      this.formError = {};
      this.paymentsData = null;
      this.paymentsLoading = true;
      axios.get(`${API_BASE_URL}/api/payments?deliveryTypeId=${this.formData.deliveryTypeId}`)
        .then((response) => { this.paymentsData = response.data; this.paymentsLoading = false; });
    },
    deliveryPrice() {
      this.price = this.deliveryData.find((item) => item.id === this.formData.deliveryTypeId).price;
    },
  },
  created() {
    this.loadDeliveries();
  },
};
</script>

<style>
.pay-error {
  font-size: 14px;
  line-height: 14px;
  color: #e02d71;
}
</style>
