export default function wordFormat(number) {
  function pluralize(n, forms) {
    const cases = [2, 0, 1, 1, 1, 2];
    return forms[(n % 100 > 4 && n % 100 < 20)
      ? 2
      : cases[Math.min(n % 10, 5)]];
  }

  const count = number;
  const forms = ['товар', 'товара', 'товаров'];

  return `${pluralize(count, forms)}`;
}
