<template>
<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<li class="catalog__item">
            <router-link class="catalog__pic" href="#" :to="{name: 'product', params: {id: product.id}}">
              <img :src="findImage" :alt="product.title">
            </router-link>

            <h3 class="catalog__title">
              <router-link href="#" :to="{name: 'product', params: {id: product.id}}">
                {{product.title}}
              </router-link>
            </h3>

            <span class="catalog__price">
              {{product.price | numberFormat}} ₽
            </span>

            <ul class="colors colors--black">
              <li class="colors__item" v-for="color in product.colors" :key="color.id">
                <label class="colors__label">
                  <input class="colors__radio sr-only" type="radio" :value="color.id" v-model="colorId">
                  <span class="colors__value" :style="{'background-color': color.color.code}" style="border: 1px solid #D3D3D3">
                  </span>
                </label>
              </li>
            </ul>
          </li>
</template>

<script>
/* eslint-disable max-len */
import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';
import { NOT_FOUND_IMG } from '@/config';

export default {
  props: ['product'],
  filters: {
    numberFormat,
  },
  data() {
    return {
      colorId: this.product.colors[0].id,
    };
  },
  methods: {
    gotoPage,
  },
  computed: {
    findImage() {
      return this.product.colors.find((item) => item.id === this.colorId).gallery ? this.product.colors.find((item) => item.id === this.colorId).gallery[0].file.url : NOT_FOUND_IMG;
    },
  },
};
</script>
